var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("蜘蛛侠")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        蜘蛛侠是由斯坦·李和史蒂夫·迪特科联合创作的虚构超级英雄，首次出现在漫画书《神奇幻想》第15期（1962年8月）中。他出现在大量的漫画、电影、动画和游戏中，在这些故事里，蜘蛛侠是彼得·帕克的别名。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        两位创作者将彼得·帕克设定为像日常生活中的大多数青少年一样，需要去处理青春期和独立经济的问题。在帕克成长的过程中也伴随着许多支持他的人物，例如梅·帕克和本·帕克，以及格温·史黛西和玛丽·简·沃森，身为蜘蛛侠的帕克也需要面对诸多的敌人，例如章鱼博士，绿魔和毒液。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        他的起源故事在被一只放射性蜘蛛咬伤后开始，帕克获得与蜘蛛有关的能力，包括紧贴表面，等比例放大的蜘蛛力量，以及用他的蜘蛛感应预感危险。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在20世纪60年代初期，超级英雄漫画书中的青少年通常被降级为主角的跟班角色，但蜘蛛侠改变了这一传统观念。他的不同之处在于，来自纽约皇后区的高中生彼得·帕克以蜘蛛侠的秘密身份为背景，年轻读者可以在彼得·帕克上体会到共鸣，作品中也一直贯彻着“能力越大，责任越大”的理念————这追溯于他的监护人，已故的叔叔本，这些都极大地影响了青少年读者，让蜘蛛侠一跃成为最受欢迎的超级英雄之一。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":"https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/marvel/wiki-spider-man.JPG?sign=420b175e18a2dc0db705521b18af856d&t=1554898243"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        1962年，随着神奇四侠的成功，漫威编辑兼首席作家斯坦·李正在为一个新的超级英雄想法而奋斗。他说蜘蛛侠的想法源于青少年对漫画书需求的激增，他想创造一个青少年可以耳熟能详的角色。在大量的漫画和影视资料中，青少年超级英雄通常给出以“男孩”结尾的名字，斯坦·李说他选择了“蜘蛛侠”，是因为他希望角色随着系列的进展而逐渐成长起来。那时斯坦·李必须得到漫威出版商马丁·古德曼的同意才能获得出版该角色的权限。在1986年的一次采访中，斯坦·李详细描述了他是如何克服古德曼各种反对意见的论点，而古德曼最终也同意了蜘蛛侠的试用。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        在蜘蛛侠出版的几个月后，出版商古德曼回顾了《神奇幻想》的销售数据，并惊讶地发现它是漫威最畅销的漫画之一。正因如此，蜘蛛侠开始拥有了属于自己的漫画期刊《神奇蜘蛛侠》(第一期出版时间为1963年)。之后《神奇蜘蛛侠》成为了漫威最畅销的漫画期刊，蜘蛛侠也因此迅速成为了一位文化偶像。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":"https://732d-s-4b10d1-1257725523.tcb.qcloud.la/web/marvel/Amazing_Fantasy_15.jpg?sign=11aa1b8200604eeb6a220feafc47de72&t=1554901677"}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span',[_vm._v("《神奇幻想》第15期（1962年8月）")])])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('v-tabs',{staticStyle:{"margin-top":"20px"},attrs:{"centered":"","color":"blue-grey lighten-5","icons-and-text":""}},[_c('v-tabs-slider',{attrs:{"color":"indigo darken-1"}}),_c('v-tab',{staticStyle:{"width":"30%"},attrs:{"href":"#tab-1"}},[_vm._v("\n      电影\n      "),_c('v-icon',[_vm._v("local_movies")])],1),_c('v-tab',{staticStyle:{"width":"30%"},attrs:{"href":"#tab-2"}},[_vm._v("\n      动画\n      "),_c('v-icon',[_vm._v("live_tv")])],1),_c('v-tab',{staticStyle:{"width":"30%"},attrs:{"href":"#tab-3"}},[_vm._v("\n      游戏\n      "),_c('v-icon',[_vm._v("videogame_asset\n")])],1),_c('v-tab-item',{attrs:{"value":"tab-1"}},[_c('v-card',{attrs:{"flat":""}},[_c('v-card-text',[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        蜘蛛侠的电影版权在1985年被哥伦比亚电影公司购买，而在1989年，索尼影视娱乐公司收购了哥伦比亚电影公司，并在之后聘请了山姆·雷米指导蜘蛛侠（2002），蜘蛛侠2（2004）和蜘蛛侠3（2007），这个系列的蜘蛛侠主演为托比·马奎尔。 2010年，索尼宣布将重启蜘蛛侠电影，其理由被广泛认为是山姆·雷米退出了原蜘蛛侠4的拍摄计划。重启的蜘蛛侠电影系列被命名为《神奇蜘蛛侠》，由安德鲁·加菲尔德主演。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        2015年2月，迪士尼，漫威工作室和索尼宣布达成分享蜘蛛侠电影版权的协议，导致《神奇蜘蛛侠》的后续拍摄被取消，新的蜘蛛侠被引入并集成到漫威电影宇宙中。汤姆·赫兰德饰演了这个年轻版的蜘蛛侠，出现在美国队长：内战（2016），蜘蛛侠：英雄归来（2017），复仇者联盟：无限战争（2018）和复仇者联盟：终结（2019），以及蜘蛛侠：远离家乡（2019）。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        索尼于2015年4月正式宣布制作蜘蛛侠动画电影的计划。蜘蛛侠：平行宇宙（2018）将计算机动画与传统的手绘漫画技术相结合，以漫威漫画中的蜘蛛小子(迈尔斯·莫拉莱斯)为灵感进行创作。《蜘蛛侠：平行宇宙》获得了普遍好评，赢得了奥斯卡最佳动画片奖(2019)，并成为蜘蛛侠系列中评价最高的电影，超越了蜘蛛侠2。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        山姆·雷米的三部曲以5.97亿美元的预算在全球票房收入25亿美元，而《神奇蜘蛛侠》系列电影票房收入超过14亿美元，预算为4.8亿美元。 漫威电影宇宙版的蜘蛛侠电影以1.75亿美元的预算得到超过8.8亿美元的票房。而《蜘蛛侠：平行宇宙》以预算为9000万美元获得超过3.74亿美元的票房。蜘蛛侠电影已在全球得到超过52亿美元的票房。")])])],1)],1),_c('v-tab-item',{attrs:{"value":"tab-2"}},[_c('v-card',{attrs:{"flat":""}},[_c('v-card-text',[_c('blockquote',{staticClass:"blockquote"},[_vm._v("\n               Spider-Man （1967-1970）"),_c('br'),_vm._v("\n              Spider-Man（1981-1982）"),_c('br'),_vm._v("\n              Spider-Man and His Amazing Friends（1981-1986）"),_c('br'),_vm._v("\n              Spider-Man: The Animated Series（1994-1998）"),_c('br'),_vm._v("\n              Spider-Man Unlimited（1999-2005）"),_c('br'),_vm._v("\n              Spider-Man: The New Animated Series（2003）"),_c('br'),_vm._v("\n              The Spectacular Spider-Man（2008-2009）"),_c('br'),_vm._v("\n              The Ultimate Spiderman（2012-2017）"),_c('br'),_vm._v("\n              Spider-Man（2017-?）"),_c('br')]),_c('br'),_c('h2',[_vm._v("客串其它动画")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('blockquote',{staticClass:"blockquote"},[_vm._v("\n                 《复仇者集结》:"),_c('br'),_vm._v("\"Vibranium Curtain\" Pt. 2"),_c('br'),_vm._v("\"Hulk's Day Out\""),_c('br'),_vm._v(" \"Avengers Disassembled\""),_c('br'),_vm._v("\"Avengers Underground\""),_c('br'),_vm._v("\"Avengers World\""),_c('br'),_vm._v("\"Vibranium Curtain\" Pt. 2"),_c('br'),_c('br'),_vm._v("\n                《浩克与海扁特工队》:"),_c('br'),_vm._v("\"The Collector\""),_c('br'),_vm._v("\"the Venom Within\""),_c('br'),_vm._v("\"Spidey, I Blew Up the Dinosaur\""),_c('br'),_vm._v("\"Days of Future Smash: The Dino Era Part 1\" "),_c('br'),_vm._v("\"Planet Monster Part 2\""),_c('br'),_c('br'),_vm._v("\n              《银河护卫队》: "),_c('br'),_vm._v("\"Back in the New York Groove\"\n\n")])])],1)],1),_c('v-tab-item',{attrs:{"value":"tab-3"}},[_c('v-card',{attrs:{"flat":""}},[_c('v-card-text',[_c('blockquote',{staticClass:"blockquote"},[_