      <h2>创作起源</h2>
      <hr class="mt-3 v-divider theme--light" >
       
       <div style="display:flex;flex-direction:row">
         <div style="display:flex;flex-direction:column">
            <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1962年，随着神奇四侠的成功，漫威编辑兼首席作家斯坦·李正在为一个新的超级英雄想法而奋斗。他说蜘蛛侠的想法源于青少年对漫画书需求的激增，他想创造一个青少年可以耳熟能详的角色。在大量的漫画和影视资料中，青少年超级英雄通常给出以“男孩”结尾的名字，斯坦·李说他选择了“蜘蛛侠”，是因为他希望角色随着系列的进展而逐渐成长起来。那时斯坦·李必须得到漫威出版商马丁·古德曼的同意才能获得出版该角色的权限。在1986年的一次采访中，斯坦·李详细描述了他是如何克服古德曼各种反对意见的论点，而古德曼最终也同意了蜘蛛侠的试用。</blockquote>
            <blockquote style="margin-top:-20px" class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;在蜘蛛侠出版的几个月后，出版商古德曼回顾了《神奇幻想》的销售数据，并惊讶地发现它是漫威最畅销的漫画之一。正因如此，蜘蛛侠开始拥有了属于自己的漫画期刊《神奇蜘蛛侠》(第一期出版时间为1963年)。之后《神奇蜘蛛侠》成为了漫威最畅销的漫画期刊，蜘蛛侠也因此迅速成为了一位文化偶像。</blockquote>
         </div>
