      <v-card flat>
        <v-card-text>
           <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;蜘蛛侠的电影版权在1985年被哥伦比亚电影公司购买，而在1989年，索尼影视娱乐公司收购了哥伦比亚电影公司，并在之后聘请了山姆·雷米指导蜘蛛侠（2002），蜘蛛侠2（2004）和蜘蛛侠3（2007），这个系列的蜘蛛侠主演为托比·马奎尔。 2010年，索尼宣布将重启蜘蛛侠电影，其理由被广泛认为是山姆·雷米退出了原蜘蛛侠4的拍摄计划。重启的蜘蛛侠电影系列被命名为《神奇蜘蛛侠》，由安德鲁·加菲尔德主演。</blockquote>
           <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2015年2月，迪士尼，漫威工作室和索尼宣布达成分享蜘蛛侠电影版权的协议，导致《神奇蜘蛛侠》的后续拍摄被取消，新的蜘蛛侠被引入并集成到漫威电影宇宙中。汤姆·赫兰德饰演了这个年轻版的蜘蛛侠，出现在美国队长：内战（2016），蜘蛛侠：英雄归来（2017），复仇者联盟：无限战争（2018）和复仇者联盟：终结（2019），以及蜘蛛侠：远离家乡（2019）。</blockquote>

           <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;索尼于2015年4月正式宣布制作蜘蛛侠动画电影的计划。蜘蛛侠：平行宇宙（2018）将计算机动画与传统的手绘漫画技术相结合，以漫威漫画中的蜘蛛小子(迈尔斯·莫拉莱斯)为灵感进行创作。《蜘蛛侠：平行宇宙》获得了普遍好评，赢得了奥斯卡最佳动画片奖(2019)，并成为蜘蛛侠系列中评价最高的电影，超越了蜘蛛侠2。</blockquote>

            <blockquote  class="blockquote">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;山姆·雷米的三部曲以5.97亿美元的预算在全球票房收入25亿美元，而《神奇蜘蛛侠》系列电影票房收入超过14亿美元，预算为4.8亿美元。 漫威电影宇宙版的蜘蛛侠电影以1.75亿美元的预算得到超过8.8亿美元的票房。而《蜘蛛侠：平行宇宙》以预算为9000万美元获得超过3.74亿美元的票房。蜘蛛侠电影已在全球得到超过52亿美元的票房。</blockquote>
